import React, { useCallback, useEffect, useRef, useState } from 'react';
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';
import CropIcon from '@mui/icons-material/Crop';
import { Button } from '@mui/material';

export interface IParentSize {
  width: string;
  height: string;
}

export type ICropImageProps = {
  getImage: any;
  setImage: (e: any) => void;
  getParentSize: IParentSize;
}

const CropImage: React.FC<ICropImageProps> = ({ getImage, setImage, getParentSize }) => {
  const [upImg, setUpImg] = useState(null as any);
  const imgRef = useRef(null);
  const previewCanvasRef = useRef(null);
  const [crop, setCrop] = useState({ unit: '%', width: 30, aspect: 16 / 9 } as any);
  const [completedCrop, setCompletedCrop] = useState(null as any);

  const onSelectFile = (e: any) => {
    if (e.target.files && e.target.files.length > 0) {
      const reader: any = new FileReader();
      reader.addEventListener('load', () => setUpImg(reader.result));
      reader.readAsDataURL(e.target.files[0]);
    }
  };

  useEffect(() => {
    if (getImage.length != 0) {
      setUpImg(getImage);
    }
  }, [getImage]);


  const onLoad = useCallback((img) => {
    imgRef.current = img;
  }, []);

  useEffect(() => {
    imgRef.current = getImage;
    return;
  }, [getImage]);

  useEffect(() => {
    if (!completedCrop || !previewCanvasRef.current || !imgRef.current || imgRef === null) {
      return;
    }

    const image: any = imgRef.current;
    const canvas: any = previewCanvasRef.current;
    const crop: any = completedCrop;

    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    const ctx = canvas.getContext('2d');
    const pixelRatio = window.devicePixelRatio;

    canvas.width = crop.width * pixelRatio * scaleX;
    canvas.height = crop.height * pixelRatio * scaleY;

    ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
    ctx.imageSmoothingQuality = 'high';

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width * scaleX,
      crop.height * scaleY
    );
  }, [completedCrop]);

  function generateDownload(canvas, crop) {
    if (!crop || !canvas) {
      return;
    }
    canvas.toBlob(
      (blob) => {
        const previewUrl = window.URL.createObjectURL(blob);

        //set up  crossorigin='anonymous' in image because of CORS!!!
        setUpImg(previewUrl);
        setImage(previewUrl)



        /*  const anchor = document.createElement('a');
        anchor.download = 'cropPreview.png';
        anchor.href = URL.createObjectURL(blob);
        anchor.click();
        window.URL.revokeObjectURL(previewUrl); */
      },
      'image/png',
      1
    );
  }


  return (
    <div>
      <canvas
        ref={previewCanvasRef}
        // Rounding is important so the canvas width and height matches/is a multiple for sharpness.
        style={{
          width: Math.round(completedCrop?.width ?? 0),
          height: Math.round(completedCrop?.height ?? 0),
          display: 'none'
        }}
      />
      
      <div style={{marginBottom:"20px", marginLeft:"20px" , marginTop:'-20px'}}>
      <Button
          variant='contained'
          disabled={!completedCrop?.width || !completedCrop?.height}
          onClick={() =>
            generateDownload(previewCanvasRef.current, completedCrop)
          }
        >
          Crop &nbsp; <CropIcon />
        </Button>
        </div>
      <ReactCrop
        src={upImg}
        style={{ width: getParentSize?.width + "px" || "680" + "px", height: getParentSize?.height || "auto" }}
        crossorigin='anonymous'
        onImageLoaded={onLoad}
        crop={crop}
        onChange={(c) => setCrop(c)}
        onComplete={(c) => setCompletedCrop(c)}
      />
     
    </div>
  );
}

export { CropImage };