import React, { useEffect, useState } from 'react';
import './UpDownFix.style.css';

/**
 * generate a div with variable x and y size
 */

export type IUpDownFixProps = {
    getX: number;
    getY: number;
    getColor: string;
}

const UpDownFix: React.FC<IUpDownFixProps> = ({ getX, getY, getColor }) => {
    const [y, setY] = useState(20);
    const [x, setX] = useState(20);
    const [color, setColor] = useState("#000000");

    useEffect(() => {
        setX(getX);
    }, [getX]);

    useEffect(() => {
       setY(getY);
    }, [getY]);

    useEffect(() => {
        setColor(getColor);
    }, [getColor]);

    const boxStyle = {
        width: `${x}px`,
        height: `${y}px`,
        backgroundColor: color
    };

    return (
        <div
            className="UpDownFix-wrapper"
            style={boxStyle}
        >
        </div>
    );
}

export { UpDownFix };
