import * as React from "react";
import Button from "@mui/material/Button";
import DialogTitle from "@mui/material/DialogTitle";
import Dialog from "@mui/material/Dialog";
import { CropImage, IParentSize } from "../CropImage/CropImage";
import { IconButton } from "@mui/material";
import CloseIcon from '@material-ui/icons/Close';
import './DialogImageCrop.style.css';
import CropIcon from '@mui/icons-material/Crop';
import DoneIcon from '@mui/icons-material/Done';

export type IDialogImageCrop = {
    getImage: string;
    sendImage: (i: string) => void
    parentImageSize: IParentSize;
}

export const DialogImageCrop: React.FC<IDialogImageCrop> = ({ parentImageSize, getImage, sendImage }) => {
    const [open, setOpen] = React.useState(false);
    const [image, setImage] = React.useState(getImage);

    const handleClickOpen = () => {
        setImage(getImage)
        setOpen(true);
    };

    React.useEffect(() => {
        setImage(getImage);
    }, [getImage]);



    const handleClose = (saveClose) => {

        if (saveClose === true) {
            sendImage(image);
        }

        setOpen(false);
    };

    return (
        <div>

            <Button variant="outlined" onClick={handleClickOpen}>
                <CropIcon />
            </Button>

            <Dialog onClose={handleClose} open={open}>
                <div className="closeIconWrapper">
                    <IconButton onClick={handleClose}>
                        <CloseIcon />
                    </IconButton>
                </div>

                {/*  <DialogTitle>Set backup account</DialogTitle> */}

                <CropImage
                    getImage={image}
                    setImage={(e) => setImage(e)}
                    getParentSize={{ width: parentImageSize.width, height: 'auto' }} />


                <div style={{ display: "flex", justifyContent: 'flex-end', marginTop:"30px",paddingBottom: '30px', paddingRight: '30px' }}>

                    <Button
                        variant='contained'
                        onClick={() => handleClose(true)}>
                        Submit &nbsp; <DoneIcon />
                    </Button>
                </div>

            </Dialog>

        </div>
    );
}
