import React, { useEffect, useRef, useState } from 'react';
import './TextMoveStyle.css';
import { useScrollBlock } from './../../services/BlockScroll/UseScrollBlock';


interface IPos {
    x: number;
    y: number;
}

export type ITextMoveProps = {
    inputText: string;
    startPosition: IPos;
    getFontSize: string;
    parentHeight: number;
}


const TextMove: React.FC<ITextMoveProps> = ({ inputText, startPosition, getFontSize, parentHeight }) => {

    const [pressed, setPressed] = useState(false);
    const [position, setPosition] = useState({ x: 0, y: 0 });
    const inputTextHolderRef = useRef() as any;
    const [text, setText] = useState("Write...");
    const [fontSize, setFontSize] = useState("2");
    const [blockScroll, allowScroll] = useScrollBlock();

    useEffect(() => {

        if (startPosition.x < 400 && startPosition.x !== 0) {
            setFontSize((Number(getFontSize) / 1.5).toString());
        }
        else {
            setFontSize(getFontSize);
        }

        let minus = 0;
        let offset = 0;
        if (startPosition.y > 0) {
            offset = 50;
            minus = inputTextHolderRef.current.clientHeight;
        }
        //set the position of the child in the center of the parent         
        setPosition({
            x: (startPosition.x - inputTextHolderRef.current.clientWidth) / 2,
            y: (startPosition.y + offset) - minus
        });

    }, [startPosition]);

    // Monitor changes to position state and update DOM and calculate the max positioning in the parent
    useEffect(() => {
        if (position.x <= 0) { position.x = 0 }
        if (position.y <= -10) { position.y = -10 } //overlapping cause the div is bigger than the text in it

        if ((startPosition.x - (inputTextHolderRef.current.clientWidth + position.x)) <= 0) { position.x = startPosition.x - inputTextHolderRef.current.clientWidth }
        if ((position.y - parentHeight) >= 0) { position.y = parentHeight }

        if (inputTextHolderRef.current) {
            inputTextHolderRef.current.style.transform = `translate(${position.x}px, ${position.y}px)`
        }

    }, [position]);

    useEffect(() => {
        setText(inputText);
    }, [inputText]);

    let previousTouch;
    const onMouseMove = (event) => {
        if (pressed) {

            switch (event.movementX !== undefined) {
                case true:

                    setPosition({
                        x: position.x + event.movementX,
                        y: position.y + event.movementY
                    })
                    break;
                case false:
                    var touch = event.touches[0];

                    if (previousTouch) {
                        event.movementX = touch.pageX - previousTouch.pageX;
                        event.movementY = touch.pageY - previousTouch.pageY;

                        setPosition({
                            x: position.x + event.movementX,
                            y: position.y + event.movementY
                        })
                    }

                    previousTouch = touch;
                    break;
            }
        }
    }

    return (
        <div
            className="inputTextHolder"
            ref={inputTextHolderRef}
            onTouchStart={() => { setPressed(true); blockScroll(); }}
            onTouchEnd={() => { setPressed(false); allowScroll(); }}
            onTouchMove={onMouseMove}

            onMouseMove={onMouseMove}
            onMouseDown={() => setPressed(true)}
            onMouseUp={() => setPressed(false)}
            onMouseLeave={() => setPressed(false)}
        >

            <h2 className="inputText" style={{ fontSize: fontSize + "em" }}>{text}</h2>
        </div>
    );
}

export { TextMove };